.header {
  min-height: 300px;
  box-sizing: border-box;
  padding: 10px 20px;
  margin-bottom: 20px;
  background-image: url(../images/header-back.jpg);
  background-repeat: no-repeat;
  background-position: top right;
  background-size: contain;

  @media (min-width: 768px) {
    background-size: 374px 384px;
  }

  @media (min-width: 1280px) {
    background-size: 725px 655px;
    height: 650px;
  }

  &__nav {
    display: flex;
    justify-content: space-between;

    @media (min-width: 768px) {
      height: 384px;
      padding-left: 20px;
    }
  }

  &__menu-opener {
    position: absolute;
    top: 33px;
    width: 18px;
    height: 12px;
    background-image: url(../images/menu.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }
}

@media (max-width: 1279px) {
  .events {
    display: none;
  }
}

@media (min-width: 1280px) {
  .events {
    font-family: 'IBM Plex Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 100%;
    color: #687480;
    transform: rotate(-90deg);
    position: absolute;
    width: 43px;
    height: 14px;
    top: 530px;
  
    &::after {
      content: "";
      position: absolute;
      top: -26px;
      right: 90px;
      width: 1px;
      height: 70px;
      background: #1A5A4C;
      transform: rotate(-90deg);
    }
  }
}
