.from-clasicism-to-romantism {
  position: relative;
  font-family: 'IBM Plex Sans';
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  color: #687480;
  display: flex;

  @media (min-width: 768px) {
    margin-left: 34px;
    margin-right: 34px;
  }

  @media (min-width: 1280px) {
    margin-left: 0;
    margin-right: 0;
  }


  &__image {
    width: 100%;
    height: 210px;

    @media (min-width: 768px) {
      height: 390px;
    }

    @media (min-width: 1280px) {
      height: 550px;
    }
  }

  &__info {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 150px;
    text-align: center;
    height: 140px;
    width: 280px;
    background: #fff;
    box-shadow: 0px 2px 0px rgba(26, 90, 76, 0.5);

    @media (min-width: 768px) {
      width: 570px;
      top: 306px;
      text-align: left;
    }

    @media (min-width: 1280px) {
      height: 205px;
      top: 448px;
      text-align: left;
    }
  }

  &__type {
    display: inline-flex;

    @media (min-width: 768px) {
      margin-left: 55px;
    }
  }

  &__data {
    padding-left: 20px;
    display: inline-flex;
    color: #1a5a4c;
    font-weight: 700;
    line-height: 150%;

    @media (min-width: 768px) {
      margin-left: 140px;
    }
  }

  &__title {
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 28px;
    line-height: 100%;
    color: #0F0E08;

    @media (min-width: 768px) {
      text-align: left;
      width: 460px;
      font-size: 36px;
      padding-left: 55px;
    }
  }
}