.logo {
  display: inline-block;
  width: 55px;
  height: 61px;
  font-weight: 700;
  font-size: 30px;
  line-height: 100%;
  text-align: center;
  color: #0f0e08;
  text-decoration: none;
}

.header {
  &__logo {
    @media (min-width: 768px) {
      position: relative;
      left: 100px;
    }
  
    @media (min-width: 1280px) {
      position: relative;
      left: 0;
      font-size: 36px;
    }
  }
}

