.gallery-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin-top: 30px;
}

.gallery {
  margin-top: 180px;

  &__title {
    text-align: center;
    font-size: 36px;
    line-height: 100%;
    color: #0f0e08;

    @media (min-width: 1280px) {
      text-align: left;
      padding-left: 55px;
    }
  }
}

.slider {
  width: 100%;
  max-width: 600px;
  height: 400px;
  margin: 20px;
  text-align: center;
  overflow: hidden;
  position: relative;

  @media (min-width: 1280px) {
    min-width: 1170px;
    min-height: 990px;
  }
}

.slides {
  display: flex;
  overflow-x: scroll;
  position: relative;
  scroll-behavior: smooth;
  scroll-snap-type: x mandatory;

  @media (min-width: 1280px) {
    flex-wrap: wrap;
    overflow-x: auto;
  }
}

.slide {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 100%;
  height: 400px;
  scroll-snap-align: center;
  margin-right: 0px;
  box-sizing: border-box;
  transform-origin: center center;
  transform: scale(1);

  @media (min-width: 1280px) {
    &__img {
      object-fit: cover;
      width: 770px;
    }
  }

  @media (min-width: 768px) {
    width: 50%;
  }

  &__img {
    max-width:100%;
    max-height:100%;
  }
}

#slides__1 {
  @media (min-width: 1280px) {
    width: 370px;
    height: 600px;
    align-items: unset;
  }
}

#slides__2 {
  @media (min-width: 1280px) {
    width: 770px;
    height: 600px;
    align-items: unset;
    margin-left: 30px;
  }
}

#slides__3 {
  @media (min-width: 1280px) {
    width: 770px;
    height: 360px;
    align-items: unset;
    margin-top: 30px;
  }
}

#slides__4 {
  @media (min-width: 1280px) {
    width: 370px;
    height: 360px;
    align-items: unset;
    margin-left: 30px;
    margin-top: 30px;
  }
}

.slide a {
  background: none;
  border: none;
}

.slider::before,
.slider::after {
  content: "";
  z-index: 1;
  background: none;
  pointer-events: none;
}

.slider__nav {
  box-sizing: border-box;
  position: absolute;
  bottom: 5%;
  left: 50%;
  width: 200px;
  margin-left: -100px;
  text-align: center;

  @media (min-width: 1280px) {
    display: none;
  }
}

.slider__navlink {
  display: inline-block;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: black;
  margin: 0 10px 0 10px;
}
