.menu {
  height: 400px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #1a5a4c;
  padding: 0px;
  padding-top: 27px;
  color: #fff;
  font-family: 'IBM Plex Sans';
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  transform: translateX(-100%);
  transition: transform 0.3s;

  @media (min-width: 768px) {
    height: 225px;
  }

  &:target {
    transform: translateX(0);
  }

  &__content {
    @media (min-width: 768px) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__items {
    padding-left: 72px;
    max-width: 320px;

    &-list {
      display: flex;
      flex-wrap: wrap;
      list-style: none;
      margin: 0;
      padding: 0;
    }
  }

  &__item {
    display: block;
    width: 100px;
    margin-right: 24px;
    margin-bottom: 24px;
  }

  &__link {
    color: #fff;
    text-decoration: none;
  }

  &__schedzhule {
    font-family: 'IBM Plex Sans';
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    padding-left: 20px;

    &::before {
      content: "";
      position: absolute;
      width: 1px;
      height: 280px;
      left: 160px;
      top: -20px;
      background: rgba(255, 255, 255, 0.5);
      transform: rotate(-90deg);

      @media (min-width: 768px) {
        top: 30px;
        height: 145px;
        left: 350px;
        transform: rotate(0deg);
      }
    }

    & p {
      @media (min-width: 768px) {
        margin-top: 0;
      }
    }

    &-list {
      list-style: none;
      padding: 0;
      & span {
        font-weight: 600;
      }
    }
  }

  &__dayoff {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    padding-left: 20px;

    @media (min-width: 768px) {
      padding-top: 35px;
      width: 170px;
      padding-left: 38px;
    }
  }

  &__cross {
    position: absolute;
    top: 33px;
    left: 26px;
    width: 13px;
    height: 13px;
    background-image: url(../images/cross.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}
