.title {

  @media (min-width: 768px) {
    position: absolute;
    top: 126px;
    padding-left: 20px;
  }
  &__name {
    padding-bottom: 20px;
    padding-left: 20px;
    font-weight: 700;
    font-size: 46px;
    line-height: 100%;
    text-align: center;
    text-transform: uppercase;

    @media (min-width: 768px) {
      font-size: 54px;
      line-height: 120%;
      text-align: left;
      width: 340px;
      color: #000;
    }

    @media (min-width: 1280px) {
      padding-top: 60px;
      font-size: 72px;
      line-height: 120%;
    }
  }

  &__tickets {
    display: flex;
    justify-content: center;
    margin-bottom: 70px;

    @media (min-width: 1280px) {
      padding-top: 90px;
      padding-left: 230px;
    }
  
    &--name {
      box-sizing: border-box;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 230px;
      height: 50px;
      border: 1px solid #1a5a4c;
      text-transform: uppercase;
      font-family: 'IBM Plex Sans';
      font-weight: 500;
      line-height: 100%;
  
      @media (min-width: 768px) {
        width: 200px;
        height: 70px;
      }
    }
  
    &--button {
      width: 50px;
      height: 50px;
      background-color: #1a5a4c;
      border: none;
  
      @media (min-width: 768px) {
        width: 70px;
        height: 70px;
      }
    }
  }
}
