.subscribtion {
  height: 386px;
  background-image: url(../images/subscrPIC.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: 130px;

  @media (min-width: 1280px) {
    margin-top: 200px;
  }

  &__title {
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: #fff;

    @media (min-width: 768px) {
      font-size: 48px;
    }
  }

  &__info {
    font-family: 'IBM Plex Sans';
    font-size: 18px;
    line-height: 150%;
    text-align: center;
    color: #fff;

    @media (min-width: 768px) {
      line-height: 100%;
    }
  }

  &__submit {
    display: flex;
    align-items: center;
    justify-content: center;

    &--form {
      display: flex;
    }

    &--input {
      box-sizing: border-box;
      width: 230px;
      height: 50px;
      border: none;

      @media (min-width: 768px) {
        width: 375px;
        height: 70px;
        padding-left: 24px;
      }

      @media (min-width: 1280px) {
        width: 470px;
        padding-left: 30px;
      }
    }

    &--button {
      width: 50px;
      height: 50px;
      background: #f14a27;
      border: none;
      padding: 0;

      @media (min-width: 768px) {
        width: 70px;
        height: 70px;
      }
    }
  }
}