.footer {
  margin: 50px 20px 0 20px;

  &__logos {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &--fb, &--insta {
      height: 24px;
      display: inline;
      margin-left: 25px;
    }
  }

  &__first-part {
    display: flex;
    justify-content: space-between;

    @media (min-width: 768px) {
      
    }
  }

  &__working-hours {
    font-family: 'IBM Plex Sans';
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    width: auto;
    margin-right: 20px;

    &-list {
      list-style: none;
      padding: 0;
      & span {
        font-weight: 600;
      }
    }

    &-dayoff {
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;

      @media (min-width: 768px) {
        width: 220px;
      }
    }
  }

  &__contacts {
    font-family: 'IBM Plex Sans';
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: #0F0E08;

    width: 130px;

    @media (min-width: 768px) {

    }
  }

  &__arrow {
    float: right;
    border: 1px  solid #1A5A4C;
    border-radius: 50%;
    height: 50px;
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  
    @media (min-width: 768px) {
      display: none;
    }
  }

  &__nav {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      width: 1px;
      height: 280px;
      left: 140px;
      top: -180px;
      background: rgba(26, 90, 76, 0.5);
      transform: rotate(-90deg);

      @media (min-width: 768px) {
        left: 50%;
      }
    }

    font-family: 'IBM Plex Sans';
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    margin-top: 115px;

    &-items {
  
      &-list {
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        margin: 0;
        padding: 0;
      }
    }

    &-link {
      color: #0f0e08;
      text-decoration: none;
    }

    &-item {
      display: block;
      width: 100px;
      margin-right: 20px;
      margin-bottom: 20px;
    }
  }

  &__rights {
    font-family: 'IBM Plex Sans';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 140.62%;
    text-align: center;
    color: #0F0E08;

    margin: 80px 0 130px;
  }

}