.current-events {
  font-family: 'IBM Plex Sans';
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #4E4E4E;
  padding: 0 20px;

  @media (min-width: 768px) {
    padding: 0 34px;
  }

  &__title {
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: #0F0E08;

    @media (min-width: 768px) {
      text-align: left;
      font-size: 48px;
    }
  }
}

.go-and-back {

  @media (min-width: 1280px) {
    display: flex;
    align-items: center;
  }

  &__image {
    margin: 0 auto;
    width: 100%;
    display: block;

    @media (min-width: 1280px) {
      position: relative;
      float: left;
      width: 50%;
      margin-right: 130px;
      margin-bottom: 105px;
    }
  }

  &__type {
    display: inline-block;
    font-family: 'IBM Plex Sans';
    font-weight: 500;
    color: #687480;
    padding-right: 50px;
  }

  &__data {
    display: inline-block;
    font-weight: 700;
    color: #1A5A4C;
  }

  &__title {
    font-family: 'Montserrat';
    font-weight: 400;
    font-size: 28px;
    line-height: 100%;
    color: #0F0E08;

    &::after {
      display: inline-block;
      content: "";
      width: 10px;
      height: 10px;
      margin-bottom: 3px;
      margin-left: 5px;
      background: #F14A27;
    }
  }

  &__content {
    margin-bottom: 60px;
  }
}

